@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500;600;700;800&display=swap);
*, *:before, *:after {
	box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  background-color: #EBEBEB;
}

.app {
  max-width: 1000px;
  margin: 0 auto;
}

.checklist-header-wrapper {
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}

.critter-checklist-header {
  font-family: 'Baloo Paaji 2', sans-serif;
  text-decoration: none;
  font-weight: 700;
  color: #7EA3CC;
  font-size: 1.2rem;
  padding: 0.8rem;
}

@media (min-width: 800px) {
  .checklist-header-wrapper {
    margin-top: 1.35rem;
  }

  .critter-checklist-header {
    font-size: 1.35rem;
  }
}

.navbar {
  width: 90%;
  margin: 0 auto;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.navlist {
  display: flex;
  justify-content: space-between;
  font-family: 'Baloo Paaji 2', sans-serif;
  font-weight: 700;
  font-size: 1.15rem;
  max-width: 600px;
  margin: 0 auto;
}

.navlink {
  text-decoration: none;
  color: #7EA3CC;
  padding: 0.25rem;
}

.navlink.active {
  color: #3A3335;
  text-decoration: none;
}

@media (min-width: 800px) {
  .navlist {
    font-size: 1.35rem;
  }
}

.filter-wrapper {
  font-family: 'Baloo Paaji 2', sans-serif;
  display: flex;
  flex-direction: column;
  background-color: #FDFDFF;
  width: 90%;
  margin: 0 auto;
  border-radius: 5px;
  padding-left: 1rem;
  padding-right: 1rem;
  transition: all 250ms ease-in-out;
}

.filter-wrapper:hover {
  background-color: #f3f3f3;
}

.filter-wrapper.open {
  padding-bottom: 1rem;
}

.filter-wrapper.open {
  background-color: #fdfdff;
}

.filter-options {
  max-height: 0;
  overflow: hidden;
}

.filter-wrapper.open > .filter-options {
  max-height: 300px;
}

.filter-wrapper.open > .filter-toggler {
  border-bottom: 2px solid #e5e5e5;
  margin-bottom: 1rem;
}

.filter-checkbox-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 0.5rem;
}

.filter-checkbox-container > .checkbox {
  font-size: 1.75rem;
}

.filter-nav {
  display: flex;
  flex-direction: column;
}

.filter-header {
  font-weight: 800;
  color: #555;
  letter-spacing: 0.5px;
}

.filter-toggler {
  font-weight: 700;
  color: #444;
  font-size: 1.2rem;
  letter-spacing: 0.5px;
  padding-top: 1rem;
  padding-bottom: 0.8rem;
  cursor: pointer;
}

.hemispheres-wrapper {
  padding-top: 1rem;
  margin-bottom: 0.6rem;
}

.hemispheres-container {
  display: flex;
  justify-content: space-between;
}

.hemisphere-header {
  font-weight: 800;
  color: #555;
  letter-spacing: 0.5px;
}

.options {
  display: flex;
}

.option-wrapper:first-child {
  padding-right: 0.5rem;
}

.option {
  background-color: #e5e5e5;
  padding: 0.15rem 0.35rem;
  border-radius: 15px;
  font-weight: 600;
  letter-spacing: 0.5px;
  transition: all 200ms ease-in-out;
  cursor: pointer;
}

.option.active {
  background-color: #9CBE89;
  color: #F9F9F9;
}

.search-bar-wrapper {
  padding-top: 1rem;
}

.search-bar {
  width: 100%;
  font-family: 'Baloo Paaji 2', sans-serif;
  color: #555;
  font-weight: 600;
  font-size: 1.1rem;
  border: 2px solid #e5e5e5;
  padding-left: 1rem;
  padding-right: 1rem;
  border-radius: 1.25rem;
  letter-spacing: 0.25px;
  transition: all 150ms ease-in-out;
}

.search-bar:focus {
  outline: none;
  border: 2px solid #7EA3CC;
}

.search-bar::-webkit-input-placeholder {
  color: #777;
}

.search-bar:-ms-input-placeholder {
  color: #777;
}

.search-bar::-ms-input-placeholder {
  color: #777;
}

.search-bar::placeholder {
  color: #777;
}


.time-input::-webkit-outer-spin-button,
.time-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.time-input-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.inputs-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.time-input {
  font-family: 'Baloo Paaji 2', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  color: #555;
  line-height: 1.2rem;
  padding-right: 0.35rem;
  padding-left: 0.35rem;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
  border: 2px solid #e5e5e5;
  border-radius: 3px;
  text-align: center;
  -moz-appearance: textfield;
  min-width: 50px;
  transition: all 150ms ease-in-out;
}

.meridian-dropdown {
  padding: 0.3rem;
  font-weight: 600;
  color: #555;
}

.time-input:focus {
  outline: none;
  border: 2px solid #7EA3CC;
}

@media (min-width: 600px) {
  .filter-header, .hemisphere-header {
    font-size: 1.1rem;
  }

  .filter-checkbox-container > .checkbox {
    font-size: 1.9rem;
  }
}

@media (min-width: 900px) {

}

.card-title, .season-header, .pill,
.time-info, .location-info, .shadow-info, .card-footer {
  font-family: 'Baloo Paaji 2', sans-serif;
}

.collectibles-wrapper {
  padding-top: 1rem;
}

.collectibles, .fossil-layout {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(275px, 1fr));
  grid-gap: 1rem;
  justify-items: center;
  width: 100%;
  margin: 0 auto;
}

.fossil-col {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}

.card-wrapper {
  min-width: 275px;
  padding: 1rem;
}

/* .card-wrapper.fossil {
  padding: 0.5rem;
} */

.card {
  background-color: #FDFDFF;
  width: 275px;
  border: 2px solid #E1DEE3;
  border-radius: 5px;
}

.card-title {
  display: block;
  padding: 0.75rem;
  text-align: center;
  font-weight: 800;
  color: #555;
  font-size: 1.25rem;
  border-bottom: 2px solid #E1DEE3;
  letter-spacing: 0.5px;
}

.fossil-component > .card-title {
  font-size: 1.1rem;
  border-bottom: none;
}

.bug-picture {
  width: 75%;
}

.music-picture {
  width: 55%;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.fish-picture {
  width: 92%;
}

.card-picture {
  display: block;
  height: auto;
  margin: 0 auto;
}

.season-info-container {
  border-top: 2px solid #E1DEE3;
  padding-top: 0.5rem;
}

.pills-container {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 0 auto;
}

.pill-wrapper {
  width: 25%;
  padding: 0.1rem;
}

.pill-wrapper:nth-child(4n + 1) {
  padding-left: 0;
}

.pill-wrapper:nth-child(4n) {
  padding-right: 0;
}

.season-header {
  display: block;
  width: 88%;
  margin: 0.15rem auto;
  color: #666;
  font-size: 1.1rem;
  font-weight: 600;
}

.pill {
  background-color: #E7E7E7;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  text-align: center;
  color: #555;
  font-weight: 600;
  border-radius: 30px;
  letter-spacing: 0.75px;
}

.pill.active {
  background-color: #9CBE89;
  color: #f9f9f9;
}

.time-info, .location-info, .shadow-info, .card-footer {
  font-weight: 600;
  color: #666;
  width: 90%;
  margin: 0 auto;
  padding-top: 1.5rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid #E1DEE3;
  font-size: 1.1rem;
}

.location-info {
  min-height: 80px;
}

.card-footer, .fossil-component:last-child {
  border: none;
  align-items: center;
}

.fossil-component {
  border-bottom: 2px solid #E1DEE3;
}

.fossil-component > .card-footer {
  padding-top: 0rem;
  margin-top: -0.25rem;
  padding-bottom: 0.5rem;

}

.season-header, .time-header, .location-header, .value, .shadow-header {
  letter-spacing: 0.5px;
}

.music-location {
  width: 100%;
  justify-content: center;
  border-top: 2px solid #E1DEE3;
  border-bottom: none;
}

.available-times {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.time:first-child {
  padding-bottom: 0.2rem;
}

.location-text {
  text-align: right;
  line-height: 1.15rem;
  padding-left: 0.1rem;
}

.music-checkbox {
  display: flex;
  justify-content: flex-end;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 0.4rem;
}

.checkbox {
  font-size: 1.9rem;
  color: #9CBE89;
  cursor: pointer;
}


